const Footer = () => (
  <div className="container">
    <img src="/static/images/instagram-logo.png" alt="instagram" />
    <img src="/static/images/linkedin-logo.png" alt="linkedin" />
    <img src="/static/images/resume.png" alt="online cv" />
    <style jsx>
      {`
        .container {
          height: 200px;
          border-top: 2px solid black;
        }
      `}
    </style>
  </div>
);

export default Footer;



